.HelpForm__Panel {
	/* # */
	direction: rtl;

	/* Auto layout */

	display: flex;
	flex-direction: column;
	padding: 24px;
	gap: 24px;

	width: 365px;
	/* height: 676px; */

	/* 12 Background/Light/02 Paper */

	background: #ffffff;
	/* Effect card */

	box-shadow: 0px 0px 2px rgba(145, 158, 171, 0.2),
		0px 12px 24px -4px rgba(145, 158, 171, 0.12);
	border-radius: 16px;

	/* Inside auto layout */

	align-self: stretch;
}

.HelpForm__Date {
	width: 100%;
}

.HelpForm__UploadBox {
	cursor: pointer;
	display: flex;
	box-sizing: border-box;

	width: 100%;
	height: 240px;

	/* 12 Background/Light/03 Neutral */

	background: #f4f6f8;
	/* 14 Others/Button & Input */

	border: 1px dashed rgba(145, 158, 171, 0.32);
	border-radius: 8px;
}

html .tox-tinymce-aux {
	font-family: "Vazirmatn RD UI FD", Vazir, Vazirmatn, "Vazirmatn";
}

/* .tox-tinymce-aux {
	font-family: "Vazirmatn RD UI FD", Vazir, Vazirmatn, "Vazirmatn" !important;
} */
.pulseCardAnimation {
	transition: box-shadow, border 0.666s ease-in-out;
	animation: pulseCard 1s ease-in-out alternate-reverse infinite;
}

@keyframes pulseCard {
	0% {
		box-shadow: 0px 5px 12px -4px rgba(145, 158, 171, 0.31);
		border: 0.5px solid #ff980044;
	}

	100% {
		/* box-shadow: 0px 5px 12px -4px #ff980066; */
		box-shadow: 0px 3px 16px -6px #ff9800bb;
		border: 1px solid #ff9800;
	}
}
