	height: 100%;
}

img {
	max-width: 100%;
}

.lds-ripple {
	display: inline-block;
	position: relative;
	width: 80px;
	height: 80px;
}
.lds-ripple div {
	position: absolute;
	border: 4px solid #1f2937;
	opacity: 1;
	border-radius: 50%;
	animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
	animation-delay: -0.5s;
}
@keyframes lds-ripple {
	0% {
		top: 36px;
		left: 36px;
		width: 0;
		height: 0;
		opacity: 0;
	}
	4.9% {
		top: 36px;
		left: 36px;
		width: 0;
		height: 0;
		opacity: 0;
	}
	5% {
		top: 36px;
		left: 36px;
		width: 0;
		height: 0;
		opacity: 1;
	}
	100% {
		top: 0px;
		left: 0px;
		width: 72px;
		height: 72px;
		opacity: 0;
	}
}

._loadingContainer {
	right: 0;
	bottom: 0;
	z-index: 99999;
	width: 100%;
	height: 100%;
	position: fixed;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: white;
}
._loadingDiv1 {
	width: 120px;
	height: 120px;
	border-radius: 25%;
	position: absolute;
	border: solid 8px #3d5aa844;
	animation: _loadingDiv1 3.2s linear infinite reverse;
}

._loadingDiv2 {
	width: 100px;
	height: 100px;
	border-radius: 25%;
	position: absolute;
	border: solid 3px #3d5aa844;
	animation: _loadingDiv2 3.2s linear infinite reverse;
}

.show-loading {
	display: flex !important;
}

@keyframes _loadingDiv2 {
	0% {
		transform: scale(1), rotate(0);
		opacity: 1;
		border-radius: 25%;
	}
	25% {
		transform: scale(1.2) rotate(270deg);
		opacity: 0.25;
		border-radius: 25%;
	}
	50% {
		transform: scale(1.2) rotate(270deg);
		opacity: 0.25;
		border-radius: 50%;
	}
	75% {
		transform: scale(1) rotate(0);
		opacity: 0.25;
		border-radius: 50%;
	}
	100% {
		transform: scale(1) rotate(0);
		opacity: 1;
		border-radius: 25%;
	}
}

@keyframes _loadingDiv1 {
	0% {
		transform: scale(1.2) rotate(270deg);
		opacity: 0.25;
		border-radius: 25%;
	}
	25% {
		transform: scale(1) rotate(0);
		opacity: 1;
		border-radius: 25%;
	}
	50% {
		transform: scale(1) rotate(0);
		opacity: 1;
		border-radius: 50%;
	}
	75% {
		transform: scale(1.2) rotate(270deg);
		opacity: 1;
		border-radius: 50%;
	}
	100% {
		transform: scale(1.2) rotate(270deg);
		opacity: 0.25;
		border-radius: 25%;
	}
}

/* //TODO */
.simplebar-placeholder {
	display: none;
}

.w-0i {
	width: 0 !important;
}

.svg-inline-color {
	fill: currentColor;
}
.ck-underline {
	position: relative;
}
.ck-underline:after {
	content: "";
	position: absolute;
	bottom: -18px;
	width: 148%;
	height: 20px;
	right: -11px;
	border: solid 5px#FF8F6D;
	border-color: #ff8f6d transparent transparent transparent;
	border-radius: 50%/20px 20px 0 0;
	transform: rotate(-6deg);
}

@keyframes fadeDown {
	0% {
		opacity: 0;
		transform: translateY(-50%);
	}

	100% {
		opacity: 1;
		transform: translateY(0);
	}
}

/* scrollbar */
/* width */
.click-scrollbar::-webkit-scrollbar,
.click-scrollbar-input .MuiInputBase-input::-webkit-scrollbar {
	width: 6px;
}

/* Track */
.click-scrollbar::-webkit-scrollbar-track,
.click-scrollbar-input .MuiInputBase-input::-webkit-scrollbar-track {
	background-color: #d8dde1;
	border-radius: 10px;
}

/* Handle */
.click-scrollbar::-webkit-scrollbar-thumb, .click-scrollbar-input .MuiInputBase-input::-webkit-scrollbar-thumb {
	background-color: #83cfff;
	border-radius: 10px;
}

/* Handle on hover */
.click-scrollbar::-webkit-scrollbar-thumb:hover, .click-scrollbar-input .MuiInputBase-input::-webkit-scrollbar::-webkit-scrollbar-thumb:hover {
	background-color: #83cfff;
}
